/* .className {css styling goes here} */
.botNav {
    width: 100%;
    height: 140px;
    align-items: center;
    background-color: #114B5F;
    display: flex;
  }

  .bot-left {
    width: 40%;
    height: 70%;
  }

  .bot-right {
    height: 70%;
    display: flex;
    width: 60%;
    border-left: solid white 2px;
  }

  .contact {
    padding-left: 10px;
    width: 50%;
    display: flex;
    align-items: center;
  }

  .contact-style {
    color: white;
    font-family: Helvetica Neue;
    font-size: 20px;
    font-weight:400;
  }

  .bot-logo {
    height: 100%;
    display:flex;
    align-items: center;
    justify-content: space-around;
    width: 50%;
  }
  
